<template>
    <div class="moreDetalWrap"> 
            <div class="back-box-custom">
                <Back v-if="false" ></Back>
                <navBarBack v-if="false"></navBarBack>
                <div class="title">{{ dataObj.label }}</div>
            </div> 
        <div class="cententBox">

            <div class="itemBox" v-if="dataObj.zonglan">
                <div class="itemContent" v-html="dataObj.zonglan"></div>
            </div>

            <div class="itemBox itemBg1"  v-if="dataObj.info.name">
                <div class="itemList" v-if="dataObj.info.name"><span class="label">中文名</span><span class="value" >{{ dataObj.info.name }}</span>
                </div>
                <div class="itemList" v-if="dataObj.info.enName"><span class="label">外文名</span><span class="value">{{ dataObj.info.enName }}</span>
                </div>
                <div class="itemList" v-if="dataObj.info.otherName.length>0"><span class="label">别名</span><span class="value">{{
                    arrToString(dataObj.info.otherName) }}</span></div>
                <div class="itemList" v-if="dataObj.info.shengri"><span class="label">生日</span><span class="value">{{ dataObj.info.shengri }}</span>
                </div>
                <div class="itemList" v-if="dataObj.info.chengdaori"><span class="label">成道日</span><span class="value">{{ dataObj.info.chengdaori
                        }}</span></div>
            </div>
            <div class="itemBox" v-if="dataObj.zhuyaoshiji">
                <div class="title">主要事迹</div>
                <div class="itemContent" v-html="dataObj.zhuyaoshiji"></div>
            </div>
            <div class="itemBox" v-if="dataObj.xinyang">
                <div class="title">信仰</div>
                <div class="itemContent" v-html="dataObj.xinyang"></div>
            </div>
            <div class="itemBox" v-if="dataObj.xingxiang">
                <div class="title">形象</div>
                <div class="itemContent" v-html="dataObj.xingxiang"></div>
            </div>
        </div>

    </div>
</template>
<script>
import request from "@/utils/axios.js";
import Back from '@/components/back-icon';
import navBarBack from "@/components/nav-bar-back";
export default {
    name: 'MoreDetail',
    components: {
        Back, navBarBack
    },
    mixins: [],
    props: {

    },
    data() {
        return {
            listData: [],
            dataObj: {
                "label": "",
                "zonglan": "",
                "zhuyaoshiji": "",
                "xinyang": "",
                "xingxiang": "",
                "info": {
                    "name": "",
                    "enName": "",
                    "otherName": [],
                    "shengri": "",
                    "chengdaori": ""
                }
            }
        }
    },
    computed: {

    },
    watch: {
        "$route": {
            handler(val) {
                this.dataObj.label = val.query.label
                this.getDetailData();
            },
            immediate: true,
        }
    },
    mounted() {

    },
    methods: {
        arrToString(arr) {
            return arr.join('、')
        },
        getDetailData() {
            request({
                url: '/json/moreDetaildata.json',
            }).then(res => {
                console.log('数据：', res, this.$route.query.label)
                if (res.code == 200) {
                    this.listData = res.data;
                    const label = this.$route.query.label;
                    const obj = this.listData.filter(item => item.label == label);
                    console.log(obj)
                    if(obj.length>0){
                        this.dataObj =obj[0];
                    }
                    console.log(this.dataObj);
                }
            })
        }
    }
};
</script>
<style lang='less' scoped>
.moreDetalWrap {
    background: #fff;
    // background: url(../../assets/images/TeamMatchProcessBg.png) no-repeat bottom;
    background-size: cover;
    perspective: 500px;
    position: relative;
    height: calc(100%);
    box-sizing: border-box;
    // padding-top: calc(28px + env(safe-area-inset-top));
    padding-bottom: env(safe-area-inset-bottom);
    color: #333;

    .back-box-custom {
        height: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        position:sticky;
        top:0;
        perspective: 500px;
        height:calc(28px + env(safe-area-inset-top));
        padding: 28px 0 10px;
        background: #fff;
    }

    .cententBox {
        padding: 10px 10px;
        
        .itemBox {
            margin-top:20px;
            .title {
                font-size: 15px;
                font-weight: 900;
                margin-bottom:10px;
            }

            .itemList {
                font-size: 14px;
                display: grid;
                grid-template-columns: 100px 1fr;
                grid-gap: 10px;

                span {
                    display: inline-block;
                }

                .label {
                    text-align: right;
                }
            }

            .itemContent {
                font-size: 14px;
                text-indent: 2em;
                line-height: 1.5;
                /deep/p { 
                    margin-bottom: 10px;
                }
            }
        }
        .itemBg1{
            background: #f8c720;
            padding: 12px 0; 
        }
    }
}
</style>